import { BpmnModelJsonHelper } from './BpmnModelJsonHelper';

export class ExtensionMethod {
    public static getAttributeValue(obj: any, attributeName: string): any {
        return BpmnModelJsonHelper.GetAttributeValue(obj, attributeName);
    }

    public static getAttributeValue4Boolean(obj: any, attributeName: string): boolean {
        return BpmnModelJsonHelper.GetAttributeValueAsBoolean(obj, attributeName);
    }

    public static getAttributeValue4Decimal(obj: any, attributeName: string): number {
        return BpmnModelJsonHelper.GetAttributeValueAsNumber(obj, attributeName);
    }

    public static getAttributeValue4String(obj: any, attributeName: string): string {
        return BpmnModelJsonHelper.GetAttributeValueAsString(obj, attributeName);
    }

    public static setAttributeValue(obj: any, attributeName: string, value: string | boolean | number | any): void {
        BpmnModelJsonHelper.SetAttributeValue(obj, attributeName, value);
    }
}
