import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';
import { BizFlowchartRefP2PMetadataEntity } from './BizFlowchartRefP2PMetadataEntity';
import { IBizFlowchartWPEntity } from '../IBizFlowchartWPEntity';

export class BizFlowchartWPEntity extends BizFlowchartRefP2PMetadataEntity implements IBizFlowchartWPEntity {
    bizEventId: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.bizEventId = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "bizEventId", this.bizEventId);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.bizEventId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "bizEventId");
    }
}