import { SlimMappingEntity } from './SlimMappingEntity';
import { ISlimCreationRuleEntity } from '../ISlimCreationRuleEntity';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';

export class SlimCreationRuleEntity extends SlimMappingEntity implements ISlimCreationRuleEntity {
    formSettingsId: string;

    protected ResetBeforeLoadFrom() {
        super.ResetBeforeLoadFrom();
        this.formSettingsId = null;
    }

    public ConvertToJsonObject() {
        let obj = super.ConvertToJsonObject();
        BpmnModelJsonHelper.SetAttributeValue(obj, "formSettingsId", this.formSettingsId);
        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.formSettingsId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "formSettingsId");
    }
}