import { IGeneralTextEntity } from '../IGeneralTextEntity';
import { Entity } from './Entity';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';

export class GeneralTextEntity extends Entity implements IGeneralTextEntity {
    text: string;
    tag: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.text = null;
        this.tag = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "text", this.text);
        BpmnModelJsonHelper.SetAttributeValue(obj, "tag", this.tag);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.text = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "text");
        this.tag = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "tag");
    }
}