import { BpmnModelJsonHelper } from '../BpmnModelJsonHelper';
import { BillDataRow } from './BillDataRow';
import { BillDataRows } from './BillDataRows';
import { BillData } from './BillData';
import { KitTable } from './KitTable';

export class BillDataTable extends KitTable {
    rows: BillDataRows = new BillDataRows();
    attachedBillData: BillData;
    attachedRow: BillDataRow;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.attachedBillData = null;
        this.attachedRow = null;
        this.rows.Clear();
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        let dataRows: any[] = [];
        for (let value of this.rows.GetList()) {
            dataRows.push(value.ConvertToJsonObject());
        }
        BpmnModelJsonHelper.SetAttributeValue(obj, "rows", dataRows);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        let dataRows: any[] = BpmnModelJsonHelper.GetAttributeValue(obj, "rows");
        for (let item of dataRows) {
            let value = new BillDataRow();
            value.LoadFromJsonObject(item);
            this.rows.Add(value);
        }
    }

    public AddRow(row: BillDataRow): void {
        this.rows.Add(row);
    }

    public GetRowMap(): { [key: string]: BillDataRow } {
        return this.rows.GetMap();
    }

    public GetRowList(): Array<BillDataRow> {
        return this.rows.GetList();
    }
}