import { BillDataRow } from './BillDataRow';

export class BillDataRows {
    private _map: { [key: string]: BillDataRow } = {};
    private _list: Array<BillDataRow> = new Array();

    get map(): { [key: string]: BillDataRow } {
        return this._map;
    }

    get list(): Array<BillDataRow> {
        return this._list;
    }

    public Add(row: BillDataRow): void {
        this._map[row.dataId] = row;
        this._list.push(row);
    }

    public Remove(row: BillDataRow): boolean {
        const bak = this._map[row.dataId];
        delete this._map[row.dataId];

        for (let i = 0; i < this._list.length; i++) {
            if (this._list[i].dataId == bak.dataId) {
                this._list.splice(i, 1);
                return true;
            }
        }

        return false;
    }

    public Clear(): void {
        this._map = {};
        this._list.length = 0;
    }

    public GetMap(): { [key: string]: BillDataRow } {
        return this._map;
    }

    public GetList(): Array<BillDataRow> {
        return this._list;
    }
}
