import { Content } from './Content';
import { IWritebackRuleEntity } from '../../entity/IWritebackRuleEntity';
import { IWritebackRuleContent } from '../IWritebackRuleContent';
import { WritebackRuleEntity } from '../../entity/impl/WritebackRuleEntity';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';

export class WritebackRuleContent extends Content<IWritebackRuleEntity> implements IWritebackRuleContent {
    mappingContent: string;

    ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();
        this.mappingContent = null;
    }

    ConvertToJsonObject() {
        let obj = super.ConvertToJsonObject();
        BpmnModelJsonHelper.SetAttributeValue(obj, "mappingContent", this.mappingContent);
        return obj;
    }

    LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);
        this.mappingContent = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "mappingContent");
    }
    
    protected GetEntityClass() {
        return WritebackRuleEntity;
    }
}
