import { BillDataField } from './BillDataField';

export class BillDataFields {
    private _map: { [key: string]: BillDataField } = {};
    private _list: Array<BillDataField> = new Array();

    get map(): { [key: string]: BillDataField } {
        return this._map;
    }

    get list(): Array<BillDataField> {
        return this._list;
    }

    public Add(field: BillDataField): void {
        this._map[field.fieldName] = field;
        this._list.push(field);
    }

    public Remove(field: BillDataField): boolean {
        const bak = this._map[field.fieldName];
        delete this._map[field.fieldName];

        for (let i = 0; i < this._list.length; i++) {
            if (this._list[i].fieldName == bak.fieldName) {
                this._list.splice(i, 1);
                return true;
            }
        }

        return false;
    }

    public Clear(): void {
        this._map = {};
        this._list.length = 0;
    }

    public GetMap(): { [key: string]: BillDataField } {
        return this._map;
    }

    public GetList(): Array<BillDataField> {
        return this._list;
    }
}
