import { JsonConvertableImpl } from '../../JsonConvertableImpl';
import { ISlimEntity } from '../ISlimEntity';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';

export abstract class SlimEntity extends JsonConvertableImpl implements ISlimEntity {
    id: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.id = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();
        BpmnModelJsonHelper.SetAttributeValue(obj, "id", this.id);
        return obj;
    }

    public LoadFromJsonObject(obj: {}) {
        super.LoadFromJsonObject(obj);
        this.id = BpmnModelJsonHelper.GetAttributeValue(obj, "id");
    }
}