import { SlimEntity } from './SlimEntity';
import { ISlimMetadataEntity } from '../ISlimMetadataEntity';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';

export abstract class SlimMetadataEntity extends SlimEntity implements ISlimMetadataEntity {
    suCode: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.suCode = null;
    }

    ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();
        BpmnModelJsonHelper.SetAttributeValue(obj, "suCode", this.suCode);
        return obj;
    }

    LoadFromJsonObject(obj: {}) {
        super.LoadFromJsonObject(obj);
        this.suCode = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "suCode");
    }
}