import { IEntity } from '../../entity/IEntity';
import { JsonConvertableImpl } from '../../JsonConvertableImpl';
import { IContent } from '../IContent';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';

export abstract class Content<TEntity extends IEntity> extends JsonConvertableImpl implements IContent<TEntity>{
    entity: TEntity;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.entity = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "entity", this.entity);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.entity = BpmnModelJsonHelper.GetAttributeValue(obj, "entity");
    }

    protected abstract GetEntityClass<ClassOfT extends TEntity>();
}