import { BillSchemaSubField } from './BillSchemaSubField';

export class BillSchemaSubFields {
    private _map: { [key: string]: BillSchemaSubField } = {};
    private _list: Array<BillSchemaSubField> = new Array();

    get map(): { [key: string]: BillSchemaSubField } {
        return this._map;
    }

    get list(): Array<BillSchemaSubField> {
        return this._list;
    }

    public Add(row: BillSchemaSubField): void {
        this._map[row.fieldName] = row;
        this._list.push(row);
    }

    public Remove(row: BillSchemaSubField): boolean {
        const bak = this._map[row.fieldName];
        delete this._map[row.fieldName];

        for (let i = 0; i < this._list.length; i++) {
            if (this._list[i].fieldName == bak.fieldName) {
                this._list.splice(i, 1);
                return true;
            }
        }

        return false;
    }

    public Clear(): void {
        this._map = {};
        this._list.length = 0;
    }

    public GetMap(): { [key: string]: BillSchemaSubField } {
        return this._map;
    }

    public GetList(): Array<BillSchemaSubField> {
        return this._list;
    }
}
