import { Content } from './Content';
import { IBizFlowchartEntity } from '../../entity/IBizFlowchartEntity';
import { IBizFlowchartContent } from '../IBizFlowchartContent';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';
import { BizFlowchartEntity } from '../../entity/impl/BizFlowchartEntity';

export class BizFlowchartContent extends Content<IBizFlowchartEntity> implements IBizFlowchartContent {
    bpmnModel: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.bpmnModel = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "bpmnModel", this.bpmnModel);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.bpmnModel = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "bpmnModel");
    }

    protected GetEntityClass() {
        return BizFlowchartEntity;
    }
}