import { BillSchemaField } from './BillSchemaField';

export class BillSchemaFields {
    private _map: { [key: string]: BillSchemaField } = {};
    private _list: Array<BillSchemaField> = new Array();

    get map(): { [key: string]: BillSchemaField } {
        return this._map;
    }

    get list(): Array<BillSchemaField> {
        return this._list;
    }

    public GetMap(): { [key: string]: BillSchemaField } {
        return this._map;
    }

    public GetList(): Array<BillSchemaField> {
        return this._list;
    }

    public Add(field: BillSchemaField): void {
        this._map[field.fieldName] = field;
        this._list.push(field);
    }

    public Remove(field: BillSchemaField): boolean {
        const bak = this._map[field.fieldName];
        delete this._map[field.fieldName];

        for (let i = 0; i < this._list.length; i++) {
            if (this._list[i].fieldName == bak.fieldName) {
                this._list.splice(i, 1);
                return true;
            }
        }

        return false;
    }

    public Clear(): void {
        this._map = {};
        this._list.length = 0;
    }
}
