import { MappingEntity } from './MappingEntity';
import { ICreationRuleEntity } from '../ICreationRuleEntity';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';

/**çåè§åå®ä½ç±»*/
export class CreationRuleEntity extends MappingEntity implements ICreationRuleEntity {
    isEnabled: boolean;
    formSettingsId: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.isEnabled = false;
        this.formSettingsId = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "isEnabled", this.isEnabled);
        BpmnModelJsonHelper.SetAttributeValue(obj, "formSettingsId", this.formSettingsId);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.isEnabled = BpmnModelJsonHelper.GetAttributeValueAsBoolean(obj, "isEnabled");
        this.formSettingsId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "formSettingsId");
    }
}