import { MetadataEntity } from './MetadataEntity';
import { IBizFlowchartEntity } from '../IBizFlowchartEntity';
import { BpmnModelJsonHelper } from '../../BpmnModelJsonHelper';

export class BizFlowchartEntity extends MetadataEntity implements IBizFlowchartEntity {
    bpmnModelContentId: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.bpmnModelContentId = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "bpmnModelContentId", this.bpmnModelContentId);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.bpmnModelContentId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "bpmnModelContentId");
    }
}