import { BillDataSubField } from './BillDataSubField';

export class BillDataSubFields {
    private _map: { [key: string]: BillDataSubField } = {};
    private _list: Array<BillDataSubField> = new Array();

    get map(): { [key: string]: BillDataSubField } {
        return this._map;
    }

    get list(): Array<BillDataSubField> {
        return this._list;
    }

    public Add(row: BillDataSubField): void {
        this._map[row.fieldName] = row;
        this._list.push(row);
    }

    public Remove(row: BillDataSubField): boolean {
        const bak = this._map[row.fieldName];
        delete this._map[row.fieldName];

        for (let i = 0; i < this._list.length; i++) {
            if (this._list[i].fieldName == bak.fieldName) {
                this._list.splice(i, 1);
                return true;
            }
        }

        return false;
    }

    public Clear(): void {
        this._map = {};
        this._list.length = 0;
    }

    public GetMap(): { [key: string]: BillDataSubField } {
        return this._map;
    }

    public GetList(): Array<BillDataSubField> {
        return this._list;
    }
}
