import { BillSchemaTable } from './BillSchemaTable';

export class BillSchemaTables {
    private _map: { [key: string]: BillSchemaTable } = {};
    private _list: Array<BillSchemaTable> = new Array();

    get map(): { [key: string]: BillSchemaTable } {
        return this._map;
    }

    get list(): Array<BillSchemaTable> {
        return this._list;
    }

    public GetMap(): { [key: string]: BillSchemaTable } {
        return this._map;
    }

    public GetList(): Array<BillSchemaTable> {
        return this._list;
    }

    public Add(table: BillSchemaTable): void {
        this._map[table.tableName] = table;
        this._list.push(table);
    }

    public Remove(table: BillSchemaTable): boolean {
        const bak = this._map[table.tableName];
        delete this._map[table.tableName];

        for (let i = 0; i < this._list.length; i++) {
            if (this._list[i].tableName == bak.tableName) {
                this._list.splice(i, 1);
                return true;
            }
        }

        return false;
    }

    public Clear(): void {
        this._map = {};
        this._list.length = 0;
    }
}
